@import "../../../scss/abstracts/variables";
.wrapper {
  max-width: 370px;
  max-height: 129px;

  background: $primaryColor;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  border-radius: 3px;
  position: relative;

  .categoriesIMg {
    width: 130.21px;
    height: 100px;

    position: absolute;
    right: 0;
    top: 0;
    object-fit: contain;
  }
  .categoriesLink {
    font-family: SF Pro Display;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    text-decoration: none;

    color: $secondaryTextColor;
  }
}
