@import "../../../scss/abstracts/variables";
.wrapperBanner {
  display: flex;
  flex-basis: 25%;
  justify-content: center;
  align-items: center;
  background-color: $accentColor;
  .imgWrapper {
    display: flex;
    flex-basis: 25%;
    .img {
      object-fit: contain;
      max-width: 50%;
    }
  }
  .title {
    flex-basis: 25%;
    font-family: SF Pro Display;
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    line-height: 36px;

    color: $primaryColor;
  }
}
