@import "../../../../scss/abstracts/variables";

.buttonFavorits {
  background-color: transparent;
  border: none;
  .radioGroup {
    width: 24px;
    height: 24px;
    margin: 5px;
    position: relative;

    & input[type="checkbox"] {
      opacity: 0;
      width: 100%;
      height: 100%;
      position: absolute;
      right: 14px;
      top: 8px;
      margin: 0;
      padding: 0;
      z-index: 1;
      cursor: pointer;
    }
    & input[type="checkbox"] + label {
      width: 100%;
      height: 100%;
      position: absolute;

      right: 14px;
      top: 8px;
    }
    & input[type="checkbox"]:checked + label {
      .Favorits {
        .svgImg {
          fill: $accentColor;
          fill-rule: nonzero;
        }
      }
    }
  }
}
