@import "../../../scss/abstracts/variables";
.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  width: 265px;
  height: 397px;

  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  border-radius: 3px;

  position: relative;
  .cardHeader {
    display: flex;
    justify-content: space-between;

    .cardHeaderText {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 90px;
      height: 30px;

      font-family: Proxima Nova;
      font-size: 12px;
      line-height: 15px;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      text-align: center;

      background-color: $accentColor;
      color: $primaryColor;
    }
  }
  .cardContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .cardImg {
      object-fit: cover;
    }
  }
  .cardFooter {
    .cardDescription {
      font-family: SF Pro Display;
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 21px;
      text-align: center;

      color: $primaryTextColor;
    }
    .cardBasket {
      background-color: $accentColor;
      border-radius: 10px 0px 0px;
      position: absolute;
      right: 0;
      bottom: 0;
      .basket {
        .basketImg {
          fill: #fff;
        }
      }
    }
  }
}
